<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Tambah Materi</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
      <ion-refresher slot="fixed" @ionRefresh="doRefresh($event)">
        <ion-refresher-content />
      </ion-refresher>
      <ion-content class="ion-padding">
        <ion-item>
          <ion-label>Judul Materi</ion-label>
          <ion-input
            v-model="title"
            placeholder="Masukkan judul materi"
          ></ion-input>
        </ion-item>
        <ion-item>
          <ion-label>Kelas</ion-label>
          <ion-select v-model="selectedClass" placeholder="Pilih Kelas">
            <ion-select-option
              v-for="kelas in classes"
              :key="kelas.id"
              :value="kelas.id"
            >
              {{ kelas.name }}
            </ion-select-option>
          </ion-select>
        </ion-item>
        <ion-item>
          <ion-label>Mata Pelajaran</ion-label>
          <ion-select
            v-model="selectedSubject"
            placeholder="Pilih Mata Pelajaran"
          >
            <ion-select-option
              v-for="subject in subjects"
              :key="subject.id"
              :value="subject.id"
            >
              {{ subject.name }}
            </ion-select-option>
          </ion-select>
        </ion-item>
        <div id="editor-container"></div>
        <ion-button style="color: white" expand="block" @click="saveMateri"
          >Simpan Materi</ion-button
        >
        <ion-grid class="ion-padding">
          <ion-row>
            <ion-col>
              <h2>Daftar Materi</h2>
              <ion-list>
                <ion-item v-for="materi in materiList" :key="materi.id">
                  <ion-label>
                    <h3>{{ materi.title }}</h3>
                    <p>Kelas: {{ materi.className }}</p>
                    <p>Mata Pelajaran: {{ materi.subjectName }}</p>
                  </ion-label>
                  <ion-button
                    style="color: white"
                    @click="openDetailModal(materi.content)"
                    >Detail</ion-button
                  >
                  <ion-button color="success" @click="openEditModal(materi)"
                    >Edit</ion-button
                  >
                  <ion-button color="danger" @click="deleteMateri(materi.id)"
                    >Hapus</ion-button
                  >
                </ion-item>
              </ion-list>
            </ion-col>
          </ion-row>
        </ion-grid>
      </ion-content>
      <ion-modal :is-open="isDetailModalOpen" @did-dismiss="closeDetailModal">
        <ion-header>
          <ion-toolbar>
            <ion-title>Detail Materi</ion-title>
            <ion-buttons slot="end">
              <ion-button @click="closeDetailModal">Close</ion-button>
            </ion-buttons>
          </ion-toolbar>
        </ion-header>
        <ion-content>
          <div v-html="detailContent"></div>
        </ion-content>
      </ion-modal>
      <ion-modal
        :is-open="isEditModalOpen"
        @did-present="handleModalDidPresent"
        @did-dismiss="closeEditModal"
      >
        <ion-header>
          <ion-toolbar>
            <ion-title>Edit Materi</ion-title>
            <ion-buttons slot="end">
              <ion-button @click="closeEditModal">Close</ion-button>
            </ion-buttons>
          </ion-toolbar>
        </ion-header>
        <ion-content>
          <div id="editor-edit-container"></div>
          <ion-button style="color: white" expand="block" @click="updateMateri"
            >Update Materi</ion-button
          >
        </ion-content>
      </ion-modal>
      <ion-toast
        :is-open="toast.isOpen"
        :message="toast.message"
        :duration="toast.duration"
        :color="toast.color"
        @did-dismiss="() => (toast.isOpen = false)"
      ></ion-toast>
    </ion-content>
  </ion-page>
</template>

<script>
import { defineComponent, onMounted, ref } from "vue";
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonBackButton,
  IonContent,
  IonRefresher,
  IonRefresherContent,
  IonButton,
  IonGrid,
  IonRow,
  IonCol,
  IonList,
  IonItem,
  IonLabel,
  IonToast,
  IonModal,
  IonInput,
  IonSelect,
  IonSelectOption,
  IonButtons,
} from "@ionic/vue";
import Quill from "quill";
import "quill/dist/quill.snow.css";
import {
  getFirestore,
  collection,
  getDocs,
  addDoc,
  updateDoc,
  doc,
  deleteDoc,
  serverTimestamp,
} from "firebase/firestore";
import { firebaseApp } from "../firebase.js";
import {
  getStorage,
  ref as storageRef,
  uploadBytes,
  getDownloadURL,
} from "firebase/storage";

const db = getFirestore(firebaseApp);
const storage = getStorage(firebaseApp);

export default defineComponent({
  name: "TambahMateri",
  components: {
    IonPage,
    IonHeader,
    IonToolbar,
    IonTitle,
    IonBackButton,
    IonContent,
    IonRefresher,
    IonRefresherContent,
    IonButton,
    IonGrid,
    IonRow,
    IonCol,
    IonList,
    IonItem,
    IonLabel,
    IonToast,
    IonModal,
    IonInput,
    IonSelect,
    IonSelectOption,
    IonButtons,
  },
  setup() {
    const toast = ref({
      isOpen: false,
      message: "",
      duration: 2000,
      color: "success",
    });
    let quill;
    let quillEdit;
    const title = ref("");
    const selectedClass = ref("");
    const selectedSubject = ref("");
    const materiList = ref([]);
    const isDetailModalOpen = ref(false);
    const isEditModalOpen = ref(false);
    const detailContent = ref("");
    const editMateriId = ref(null);
    const editContent = ref("");

    const classes = ref([]);
    const subjects = ref([]);

    onMounted(() => {
      quill = new Quill("#editor-container", {
        theme: "snow",
        modules: {
          toolbar: {
            container: [
              [{ header: [1, 2, false] }],
              ["bold", "italic", "underline"],
              ["image", "video", "audio"],
              [{ list: "ordered" }, { list: "bullet" }],
              [
                { align: "" },
                { align: "center" },
                { align: "right" },
                { align: "justify" },
              ],
            ],
            handlers: {
              image: () => {
                selectLocalImage();
              },
              video: () => {
                insertVideoUrl();
              },
              audio: () => {
                insertAudioUrl();
              },
            },
          },
        },
      });
      fetchClasses();
      fetchSubjects();
      fetchMateri();
    });

    const fetchClasses = async () => {
      const querySnapshot = await getDocs(collection(db, "kelas"));
      classes.value = querySnapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));
    };

    const fetchSubjects = async () => {
      const querySnapshot = await getDocs(collection(db, "mataPelajaran"));
      subjects.value = querySnapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));
    };

    const fetchMateri = async () => {
      try {
        const querySnapshot = await getDocs(collection(db, "materi"));
        materiList.value = querySnapshot.docs.map((doc) => {
          const data = doc.data();
          return {
            id: doc.id,
            ...data,
            className:
              classes.value.find((kelas) => kelas.id === data.class)?.name ||
              "N/A",
            subjectName:
              subjects.value.find((subject) => subject.id === data.subject)
                ?.name || "N/A",
          };
        });
      } catch (error) {
        console.error("Error fetching materi:", error);
        showToast("Terjadi kesalahan saat mengambil data materi.", "danger");
      }
    };

    const saveMateri = async () => {
      const materiContent = quill.root.innerHTML;
      if (
        !title.value ||
        !selectedClass.value ||
        !selectedSubject.value ||
        !materiContent
      ) {
        showToast("Semua field harus diisi", "danger");
        return;
      }
      try {
        await addDoc(collection(db, "materi"), {
          title: title.value,
          class: selectedClass.value,
          subject: selectedSubject.value,
          content: materiContent,
          createdAt: serverTimestamp(),
        });
        showToast("Materi berhasil disimpan", "success");
        quill.root.innerHTML = ""; // Clear the editor
        title.value = "";
        selectedClass.value = "";
        selectedSubject.value = "";
        fetchMateri(); // Fetch the updated list of materi
      } catch (error) {
        console.error("Error saving materi: ", error);
        showToast("Terjadi kesalahan saat menyimpan materi", "danger");
      }
    };

    const deleteMateri = async (materiId) => {
      try {
        await deleteDoc(doc(db, "materi", materiId));
        showToast("Materi berhasil dihapus", "success");
        fetchMateri(); // Fetch the updated list of materi
      } catch (error) {
        console.error("Error deleting materi: ", error);
        showToast("Terjadi kesalahan saat menghapus materi", "danger");
      }
    };

    const openDetailModal = (content) => {
      detailContent.value = content;
      isDetailModalOpen.value = true;
    };

    const openEditModal = (materi) => {
      editMateriId.value = materi.id;
      editContent.value = materi.content;
      isEditModalOpen.value = true;
    };

    const closeDetailModal = () => {
      isDetailModalOpen.value = false;
    };

    const closeEditModal = () => {
      isEditModalOpen.value = false;
    };

    const handleModalDidPresent = () => {
      if (isEditModalOpen.value) {
        quillEdit = new Quill("#editor-edit-container", {
          theme: "snow",
          modules: {
            toolbar: {
              container: [
                [{ header: [1, 2, false] }],
                ["bold", "italic", "underline"],
                ["image", "video", "audio"],
                [{ list: "ordered" }, { list: "bullet" }],
                [
                  { align: "" },
                  { align: "center" },
                  { align: "right" },
                  { align: "justify" },
                ],
              ],
              handlers: {
                image: () => {
                  selectLocalImageForEdit();
                },
                video: () => {
                  insertVideoUrlForEdit();
                },
                audio: () => {
                  insertAudioUrlForEdit();
                },
              },
            },
          },
        });
        quillEdit.root.innerHTML = editContent.value;
      }
    };

    const updateMateri = async () => {
      const updatedContent = quillEdit.root.innerHTML;
      if (!updatedContent) {
        showToast("Isi materi tidak boleh kosong", "danger");
        return;
      }
      try {
        await updateDoc(doc(db, "materi", editMateriId.value), {
          content: updatedContent,
        });
        showToast("Materi berhasil diperbarui", "success");
        closeEditModal();
        fetchMateri(); // Fetch the updated list of materi
      } catch (error) {
        console.error("Error updating materi: ", error);
        showToast("Terjadi kesalahan saat memperbarui materi", "danger");
      }
    };

    const selectLocalImage = () => {
      const input = document.createElement("input");
      input.setAttribute("type", "file");
      input.setAttribute("accept", "image/*");
      input.click();

      input.onchange = async () => {
        const file = input.files[0];
        const imageRef = storageRef(storage, `images/${file.name}`);
        await uploadBytes(imageRef, file);
        const downloadURL = await getDownloadURL(imageRef);
        const range = quill.getSelection();
        quill.insertEmbed(range.index, "image", downloadURL);
      };
    };

    const selectLocalImageForEdit = () => {
      const input = document.createElement("input");
      input.setAttribute("type", "file");
      input.setAttribute("accept", "image/*");
      input.click();

      input.onchange = async () => {
        const file = input.files[0];
        const imageRef = storageRef(storage, `images/${file.name}`);
        await uploadBytes(imageRef, file);
        const downloadURL = await getDownloadURL(imageRef);
        const range = quillEdit.getSelection();
        quillEdit.insertEmbed(range.index, "image", downloadURL);
      };
    };

    const insertVideoUrl = () => {
      const videoUrl = prompt("Masukkan URL video (YouTube):");
      if (videoUrl) {
        const range = quill.getSelection();
        const youtubeEmbed = `<iframe width="560" height="315" src="${videoUrl.replace(
          "watch?v=",
          "embed/"
        )}" frameborder="0" allowfullscreen></iframe>`;
        quill.clipboard.dangerouslyPasteHTML(range.index, youtubeEmbed);
      }
    };

    const insertVideoUrlForEdit = () => {
      const videoUrl = prompt("Masukkan URL video (YouTube):");
      if (videoUrl) {
        const range = quillEdit.getSelection();
        const youtubeEmbed = `<iframe width="560" height="315" src="${videoUrl.replace(
          "watch?v=",
          "embed/"
        )}" frameborder="0" allowfullscreen></iframe>`;
        quillEdit.clipboard.dangerouslyPasteHTML(range.index, youtubeEmbed);
      }
    };

    const insertAudioUrl = () => {
      const audioUrl = prompt("Masukkan URL audio:");
      if (audioUrl) {
        const range = quill.getSelection();
        const audioEmbed = `<audio controls><source src="${audioUrl}" type="audio/mpeg">Your browser does not support the audio element.</audio>`;
        quill.clipboard.dangerouslyPasteHTML(range.index, audioEmbed);
      }
    };

    const insertAudioUrlForEdit = () => {
      const audioUrl = prompt("Masukkan URL audio:");
      if (audioUrl) {
        const range = quillEdit.getSelection();
        const audioEmbed = `<audio controls><source src="${audioUrl}" type="audio/mpeg">Your browser does not support the audio element.</audio>`;
        quillEdit.clipboard.dangerouslyPasteHTML(range.index, audioEmbed);
      }
    };

    const showToast = (message, color) => {
      toast.value = {
        isOpen: true,
        message: message,
        duration: 2000,
        color: color,
      };
    };

    return {
      title,
      selectedClass,
      selectedSubject,
      saveMateri,
      deleteMateri,
      materiList,
      toast,
      isDetailModalOpen,
      isEditModalOpen,
      detailContent,
      openDetailModal,
      openEditModal,
      closeDetailModal,
      closeEditModal,
      handleModalDidPresent,
      updateMateri,
      classes,
      subjects,
    };
  },
  methods: {
    doRefresh(event) {
      setTimeout(() => {
        event.target.complete();
        this.fetchMateri();
      }, 2000);
    },
  },
});
</script>

<style>
#editor-container,
#editor-edit-container {
  height: 200px;
}
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

th,
td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #f2f2f2;
  font-weight: bold;
}

tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}

tbody tr:hover {
  background-color: #f1f1f1;
}
</style>
